import React, { Component } from 'react';
import ReactSVG from 'react-svg'
import './Sender.scss'
import '../../../../Config'
import * as firebase from 'firebase';
class receiver extends Component {
  constructor(props) {
    super(props);
      
  }

  render() {

    var test = "";
    if (this.props.msg.isseen) {
      test = "sof"
    } else { test = "mar" }

    console.log(this.props.msg, "this.props.msg")
    return (
      <div className="sendContainer">

        <div className="sendHeader">
          {/* <p className="nom" > {this.props.msg.sender} </p> */}
          <p className="time">
            {this.props.msg.time}
            {/* {test} */}
          </p>
        </div>


        <div className="sendcontaint">

          <div className="messageSend" >
            <p> {this.props.msg.message} </p>



            <ReactSVG
              src="/img/chat/seen.svg"
              className="svgIconSeensend"
            />


          </div>






        </div>


      </div>
    );
  }

}

export default receiver;