import React, { Component } from 'react';
import logo from '../../assets/logo.svg';
import './App.scss';
import Sidebar from '../Sidebar';
import Header from '../Header';
import LoginPage from '../LoginPage';
import { UserContext } from '../Context'
import { BrowserRouter } from "react-router-dom";
import Routers from '../Routes'
import { callApi } from '../../Helpers';



class App extends Component {
  constructor(props, context) {
    super(props, context)

    this.state = {
      user: this.context,
      redirect:false
    }
    this.changeUser = this.changeUser.bind(this)
    this.logOut = this.logOut.bind(this)
  }
  // componentDidMount(){
  //   setTimeout(
  //       function() {
  //           this.setState({redirect: true});
  //       }
  //       .bind(this),
  //       30000
  //   );
  // }
  async changeUser(user, hotel=43) {
    console.log(user)

    this.setState({ user: { ...user } })
    localStorage.setItem('user', JSON.stringify(user))
    if(user.role==3){
      let hotels = await callApi('hotels')
      user.hotel_id = [44, ...hotels.data.map(x=>x.id)]
      console.log(user)
    }
    localStorage.setItem('user', JSON.stringify(user))
    this.setState({ user: { ...user } })
  }

  logOut() {
    this.setState({ user: { auth: false } })
    localStorage.setItem('user', JSON.stringify({ auth: false }))
  }

  render() {
    // if(this.state.redirect)
    //     return <LoginPage changeUser={this.changeUser} />
    return (
      <BrowserRouter>
        <UserContext.Provider value={this.state.user}>
          <UserContext.Consumer>
            {
              user => user.auth ?
                <>
                
                  <Header changeUser={this.changeUser} user={user}></Header>
                  <Sidebar logOut={this.logOut} user={user}></Sidebar>
                  <Routers />
                 
                  
                </>
                :
                <LoginPage changeUser={this.changeUser} />
            }
          </UserContext.Consumer>
        </UserContext.Provider>
      </BrowserRouter>
    );
  }
}
App.contextType = UserContext
export default App;
