import React, { Component } from 'react';
import ReactSVG from 'react-svg'
import './Inbox.scss'
import { withRouter } from 'react-router-dom';
import '../../../../Config'
import * as firebase from 'firebase';
import NotificationBadge from 'react-notification-badge';
import { Effect } from 'react-notification-badge';
class Inbox extends Component {
  constructor(props) {
    super(props);
    this.state = {
      chats: { ...this.props.chats },
      inBox: { ...this.props.inBox },
      count: '',
    }
    this.SetSeen = this.SetSeen.bind(this)
  }



  SetSeen = () => {

    var db = firebase.database();
    var query = db.ref("Chats").orderByChild("sender").equalTo(this.props.inBox.inBox);
    query.on("child_added", function (snapshot) {
      snapshot.ref.update({ isseen: "true" })
    });
  }


  render() {

    console.log(this.props.inBox.inBox, "inbox2")
    console.log(this.props.me, "this.props.me]")
    console.log(this.props.tourists.id, "mutourists")
    console.log(this.props.chat.sender, "sendertourists")
    console.log(this.props.chat.receiver, "reccccccc")
    console.log(this.props.chats, "chats f coontact")
    console.log(this.props.chats.filter(user => (user.sender == this.props.chat.sender)), "chats filtrer coontact")
    var allMsgSender = this.props.chats.filter(user => (user.sender == this.props.chat.sender))
    console.log(allMsgSender, "allMsgSender")
    console.log(allMsgSender.filter(x => x.isseen == "false"), "msg not seen")






    console.log(this.props.inBox.inBox, "inbox3")
    return (
      <div className="InboxContainer" onClick={() => { this.props.setInBox({ inBox: this.props.chat.sender }).then(() => { this.SetSeen() }) }}  >


        <div className="ImageMsg">
          <img src="/img/chat/contact.png" className="imgContact" />
        </div>

        <div className="TextMsg">
          {this.props.tourists.map(name => {
            return name.id == this.props.chat.sender ?
              <p className="nameContact"> {name.nom}</p>
              : ""
          })}
          {/* <p className="nameContact"> {this.props.chat.sender}</p> */}
          <p className="msgContact">
            {this.props.chat.message}

          </p>
        </div>

        {/* <div className="TimeMsg"> */}
        {/* <p>{this.props.chat.time}</p> */}
        {/* <ReactSVG
              src="/img/chat/notSeen.svg"
              className="svgIconSeensend"
            />
 </div>
       */}


        <div className="msg_not_seen">

          <NotificationBadge count={allMsgSender.filter(x => x.isseen == "false").length} effect={Effect.SCALE} />
        </div>

      </div>
    );
  }

}

export default withRouter(Inbox);