import React, { Component } from 'react';
import './AddRoomForm.scss'
import { callApi } from '../../../Helpers';
import { callApiWithBody } from '../../../Helpers';

import { UserContext } from '../../Context'
import { Redirect } from 'react-router-dom'
import Modal from 'react-awesome-modal';
class AddRoomForm extends Component {
	constructor(props, context) {
		super(props, context)
		this.state = {
			room: {
				room_number: '',
				section: '',
				floor: '',
				capacity: '',
				hotel_id: this.context.hotel_id[0],
				status: '1',

			},
			alert_message: [],
			sucess_message: '',
			visible: '',
			devices: []


		}
		this.setData = this.setData.bind(this);
		this.submit = this.submit.bind(this);
	}
	async componentDidMount() {
		let rooms = await callApi('devices/available', {}, 'GET');
		this.setState({ rooms: rooms.data });
		console.log('state', this.state.rooms);
	}
	closeModal() {
		this.setState({
			visible: false
		});
	}

	async submit() {

		await this.setState({ alert_message: [] })

		if (!/^[0-9]*$/.test(this.state.room.room_number)) {

			this.setState({ alert_message: [...this.state.alert_message, "Invalid Room Number!"] })
		}
		if (!/[A-Za-z]{1,100}/.test(this.state.room.section)) {

			this.setState({ alert_message: [...this.state.alert_message, "Invalid Section!"] })
		}
		if (!/^[0-9]*$/.test(this.state.room.floor)) {

			this.setState({ alert_message: [...this.state.alert_message, "Invalid Floor Input!"] })
		}
		if (!/^[0-9]*$/.test(this.state.room.capacity)) {

			this.setState({ alert_message: [...this.state.alert_message, "Invalid Capacity Input!"] })
		}
		let rooms = await callApi('rooms', { hotel_id: this.context.hotel_id[0] })
		for (var i = 0; i < rooms.data.length; i++) {
			if (this.state.room.room_number == rooms.data[i].room_number) {
				this.setState({ alert_message: [...this.state.alert_message, "room already book!"] });
			}
		}
		if (this.state.alert_message.length == 0)

			var x = await callApi('/room', this.state.room, 'POST');

		if (this.state.room.room_number) {

			this.setState({ room_id: x.data[0].id, hotel_id: x.data[0].hotel_id });

			let request = {
				room_id: this.state.room_id,
				hotel_id: this.state.hotel_id,
				device_id: this.state.deviceId
			};
			await callApi('/device_room/attach', request, 'POST');
			if (this.state.deviceId !== undefined)
				await callApi('room/' + this.state.room_id, { linked_to_device: 1 }, 'PUT');
		}

		if (x.data == undefined) {
			this.setState({ visible: "echec" })
		} else {
		setTimeout(()=>document.location.href = "/rooms",1000)
		this.setState({ visible: "success" })
		}

	}
	setData(room) {
		this.setState({ room: { ...this.state.room, ...room } });
	}

	render() {

		return <div>


			<div className="message-erreur" >
				{this.state.alert_message.map(alert => <p>{alert}</p>)}
			</div>
			<div className='room-form-add'>
				<div className="room-header">
					<div><img src="/img/ui/add_room.png" /></div>
					<div>Add Room</div>
				</div>
				<div className='room-form-add'>
					<div className="room-inputs">
						<input type="text" placeholder="Room Number" onChange={(e) => this.setData({ room_number: e.target.value })} required />
						<input type="text" placeholder="Section" onChange={(e) => this.setData({ section: e.target.value })} pattern="[A-Za-z]{1,100}" required />
						<input type="number" placeholder="Floor" onChange={(e) => this.setData({ floor: e.target.value })} required />
						<input type="number" placeholder="Capacity" onChange={(e) => this.setData({ capacity: e.target.value })} required />
						<button onClick={this.submit}>
							<img src="/img/ui/valid.png" />
							Add
          </button>
						{this.state.visible == "success" ?
							<Modal
								className="my-modal"
								visible={this.state.visible}
								width="400"
								height="300"
								effect="fadeInUp"
								onClickAway={() => this.closeModal()}
							>
								<div className="valide-modal">
									<img src="/img/ui/succes.png" style={{ width: "32", height: "32" }} />
									<div>successfully added !</div>
									<button onClick={() => this.closeModal()} >X</button>
								</div>
							</Modal>
							: null}
						{this.state.visible == "echec" ?
							<Modal
								className="my-modal"
								visible={this.state.visible}
								width="400"
								height="300"
								effect="fadeInUp"
								onClickAway={() => this.closeModal()}
							>
								<div className="echec-modal">
									<img src="/img/ui/echec.png" style={{ width: "32", height: "32" }} />
									<div>Reinsert information please !</div>
									<button onClick={() => this.closeModal()} >X</button>
								</div>
							</Modal>
							: null}
					</div>
				</div>
			</div>
		</div>
	}
}

AddRoomForm.contextType = UserContext

export default AddRoomForm;
