import React, { Component } from 'react';
import './EmployeeEditForm.scss'
import { callApi } from '../../../Helpers';
import { UserContext } from '../../Context'
import ReactSVG from 'react-svg';

class EmployeeEditForm extends Component {
  constructor(props, context) {
    super(props, context)
    this.state = {
      role: 2,
      approved: 1,
      hotel_id: this.context.hotel_id[0],
      employee: {},
      permissions:[],
    }
    this.edit = this.edit.bind(this)
  }


  async componentDidMount() {
    let x = await callApi('user/' + this.props.match.params.id)
    this.setState({ employee: x.data[0] })
    x = await callApi('permission_options',{user_id:x.data[0].id})
    console.log(x)
    this.setState({permissions: x.data});
    }

  async edit() {

    this.state.permissions.forEach(x=>{
      callApi('permission/'+x.id,{...x},'PUT')
    })
    let x = await callApi('user/'+this.props.match.params.id,
      {
        ...this.state
      },
      'PUT')

    //  console.log(this.state,"state up date")
    //  console.log(this.state.first_name,"is the new first name")
    // if (x.data)
      // window.location.href = "/employees"
    // else
      // alert("There has been an error please try again")
  }

  updatePermission = (i,j)=>{
    
    this.setState({permissions:this.state.permissions.map((y,k)=>i==k?{...y,level:j}:y)})
    callApi('permission/'+this.state.permissions[i].id,{...this.state.permissions[i],level:j},'PUT')
  }

  render() {

    return <div className='employee-form-add'>

      <div className="employee-header">

        <div className="employee-header_title">Edit Employee</div>

      </div>

      <div className="employee-inputs">
        <input defaultValue={this.state.employee.name} placeholder="Name" onInput={e => this.setState({ name: e.target.value })} />
        <input defaultValue={this.state.employee.email} type="text" placeholder="Email" onInput={e => this.setState({ email: e.target.value })} />
        <input type="password" placeholder="Password" onInput={e => this.setState({ password: e.target.value })} />
        <button onClick={this.edit}>
          <img src="/img/ui/valid.png" />
          Update
        </button>
        <table>
          <thead>
            <tr>
              <th>Permission</th>
              <th>Edit</th>
              <th>View</th>
              <th>Forbidden</th>
            </tr>
          </thead>
          <tbody>
            {this.state.permissions.map((x,i)=>
              <tr style={{height:'54px'}}>
                <td>{x.text}</td>
                <td onClick={()=>this.updatePermission(i,2)}>{x.level==2&&<ReactSVG src="/img/ui/check.svg"/>}</td>
                <td onClick={()=>this.updatePermission(i,1)}>{x.level==1&&<ReactSVG src="/img/ui/check.svg"/>}</td>
                <td onClick={()=>this.updatePermission(i,0)}>{x.level==0&&<ReactSVG src="/img/ui/check.svg"/>}</td>
              </tr>
            )}
          </tbody>
        </table>
      </div>

    </div>;
  }

}

EmployeeEditForm.contextType = UserContext

export default EmployeeEditForm;
