import React, { Component } from 'react';
import { callApiWithBody, callApi } from '../../Helpers';
import './Notifications.scss';
import ImageUploader from 'react-images-upload';
import Popup from 'reactjs-popup';
import { UserContext } from '../Context'

class Notifications extends Component {
  constructor(props, context) {
    super(props, context)
    this.state = { "image_right_url": 'https://i.imgur.com/3uIohbn.jpg', "expandeble_text": "expanded textt", "sound": 'ripple' }
    this.notify = this.notify.bind(this)
    this.upload = this.upload.bind(this)
  }

  async notify() {
    let image = await callApi('notif_hotels_devices', {
      hotel_id: this.context.hotel_id[0],
      data: this.state
    }, 'POST');
  }
  async upload(e) {
    let data = new FormData()
    data.append('image', document.getElementsByName('image')[0].files[0])
    let image = await callApiWithBody('upload', data, 'POST')
    this.setState({ image: image.image });
  }

  ellipsis(promo) {
    promo = String(promo);
    return promo.substring(0, 3);
  }
  render() {
    return <div><div className="container">
      <div id="c15-phone">
        <p>Preview</p>
        <div className="preview">
          <div className="arrow-right" style={{ display: !this.state.image ? 'none' : '' }}></div>
          <div className="price">
            <span className="price">{this.state.price}<sup style={{ display: this.state.price ? 'inline' : 'none' }}>Dt</sup></span>
            <span className="promo" style={{ display: this.state.price_promo ? 'inline' : 'none' }}>
              {this.state.price_promo && this.state.price_promo.length > 3 ? this.ellipsis(this.state.price_promo) : this.state.price_promo}%</span>
          </div>
          <img src={this.state.image ? this.state.image : "/img/ui/no-photo.png"} style={{ borderLeft: !this.state.image ? '1px dashed gray' : '' }} alt="Preview Template" />
          <h2>{this.state.title}</h2>
          <p>{this.state.summery}</p>
        </div>
        <img src="/img/ui/c15pro.png" alt="mobile-phone" />
      </div>
      <div className="forms">
        <div id="target-container">

          <div className="title-target">

            <p>Target</p>

          </div>

          <div className="body-target">

            <p>Notify everyone or specify a target</p>

            <div className="target">

              <label className="target-label">

                <div className="radio">

                  <div className="radio-content" >

                    <img src="/img/ui/all-the-world.png" alt="people-image" />

                    <input type="radio" name="target" />

                    <span>Everyone</span>

                    <span className="checkmark"></span>
                  </div>

                </div>

              </label>

              <div className="button">



                <Popup trigger={<button className="button-target" id="button-target"> <div className="image"><img src="/img/ui/target.png" alt="target" /></div> <div className="text">Create a target</div></button>} modal>

                  {close => (
                    <div className="popup">
                      <div className="popup-interne">
                        <div className="title-modal">
                          <div className="text">
                            <p>Create a target</p>
                          </div>
                          <div className="cross" id="cross" >
                            <span onClick={() => { close(); }}>&times;</span>
                          </div>
                        </div>
                        <div className="body-modal">
                          <div className="platform">





                          </div>
                          <div className="first-button">
                            <button className="add-bt">
                              <div className="add-condition">
                                <div className="icon">
                                  <img src="/img/ui/plus.png" alt="add" />
                                </div>
                                <div className="text">
                                  Add a condition
                        </div>
                              </div>
                            </button>
                          </div>
                          <div className="second-button">
                            <button className="save-button">
                              <div className="save-target">
                                <div className="icon">
                                  <img src="/img/ui/down-arrow.png" alt="save" />
                                </div>
                                <div className="text">
                                  Save the target for the future
                        </div>
                              </div>
                            </button>
                          </div>

                        </div>

                        <div className="confirm-container">
                          <div className="confirm-button">
                            <button>
                              <div className="confirm">
                                <div className="image">
                                  <img src="/img/ui/tick.png" alt="tick" />
                                </div>
                                <div className="text">
                                  Validate
                        </div>
                              </div>
                            </button>
                          </div>
                        </div>
                      </div>
                    </div>
                  )}

                </Popup>

              </div>

            </div>

          </div>

        </div>

        <div id="notification-container">

          <div className="title-notification">

            <p>NOTIFICATION</p>

          </div>

          <div className="body-notification">

            <p>Message Details</p>

            <div className="first-container">
              <div className="text-container">
                <div className="title">
                  <label for="title">
                    <p>Title*:</p>
                  </label>
                  <input onInput={e => this.setState({ title: e.target.value })} type="text" name="notification_title" id="title" />


                </div>
                <div className="description">
                  <label for="description">
                    <p>Description*:</p>
                  </label>
                  <textarea onInput={e => this.setState({ summery: e.target.value })} name="notification_description" value={this.state.summery} id="description">{this.state.summery}</textarea>
                </div>
              </div>
              <div className="image-container">
                <p>Image:</p>
                <ImageUploader
                  fileContainerStyle={{ height: '152px', marginRight: '17px' }}
                  onChange={this.upload}
                  label={'Max file size: 2MB | Allowed formats: jpg, png'}
                  maxFileSize="2097152"
                  name="image"
                />
              </div>
            </div>

            <div className="second-container">
              <div className="price">
                <label for="price">
                  <p>Price:</p>
                </label>
                <input type="text" onInput={e => this.setState({ price: e.target.value })} name="notification_price" id="price" placeholder="DT" />
              </div>
              <div className="promo">
                <label for="promo">
                  <p>Promo:</p>
                </label>
                <input type="text" name="notification_promo" onInput={e1 => this.setState({ price_promo: e1.target.value })} id="promo" placeholder="%" />
              </div>
            </div>
            <button onClick={this.notify} className="send-button" id="send-button">

              <div className="image"><img src="/img/ui/target.png" alt="target" /></div>

              <div className="text">Send</div>

            </button>
          </div>

        </div>
      </div>
    </div>
    </div>
  }



}

Notifications.contextType = UserContext

export default Notifications;
