import React, { Component } from 'react';
import './DeviceCard.scss'
import {Link} from 'react-router-dom'
import { UserContext } from '../../Context';

class DeviceCard extends Component {

  constructor(props,context){
    super(props, context)
  }

  render() {
    console.log(this.props.device)
    return <div className="device-card">
      <div className="device-details">
        <p>IMEI: {this.props.device.imei} / Room: {this.props.device.device_room.room.room_number} / </p>
        <p>Customer: {this.props.device.device_room.stay.tourist.name} /</p>
        {this.context.role==3&&(<p>Hotel ID: {this.props.device.hotel_id}</p>)}
        <p style={{ color: this.props.device.status == 0 ? 'red' : 'green' }}><span style={{ backgroundColor: this.props.device.status == 0 ? 'red' : 'green' }}></span>   {this.props.device.status == 0 ? 'Inactive' : 'Active'}</p>
      </div>
      <div className="device-button">
        <Link to={'/edit/device/'+this.props.device.id}>
          <button>Edit</button>
        </Link>
      </div>
    </div>;
  }
}
DeviceCard.contextType = UserContext

export default DeviceCard;
