import React, { Component } from 'react';
import ReactSVG from 'react-svg'
import './RestaurantCard.scss'
import { Redirect } from 'react-router-dom'
import Switch from '../Switch';
import EditButton from '../EditButton';
import PostInformation from '../PostInformation';
import ReactDOM from 'react-dom';
import Modal from 'react-modal';


class RestaurantCard extends Component {
  constructor(props) {
    super(props)
    this.state = {
      redirect: false,
    }
  }

  render() {
    if (this.state.redirect)
      return <Redirect to={'/restaurant/' + this.props.id}></Redirect>
    return <div className="restaurant-card">
      <div className="description">
        <ReactSVG
          src="/img/restaurant/dish.svg"
        />
        <p className="title" onClick={() => this.setState({ redirect: true })}>
          {this.props.title}
        </p>
        <p className="desc">
          {this.props.description || 'This restaurant doesn\'t have a description'}
        </p>
      </div>

      <div className="div_buttons">
        <EditButton link={this.props.link} id={this.props.id}>Edit</EditButton>
        <PostInformation title={this.props.title} description={this.props.description} image={this.props.image} />
        <div><img src="/img/ui/delete_button.png" onClick={() => this.props.onDelete(this.props.id)} /></div>
      </div>

      <div style={{ backgroundImage: `url(${this.props.image})` }} className="image">
        <Switch id={this.props.id} check={this.props.check}></Switch>
      </div>
    </div>;
  }
}

export default RestaurantCard;
