import React, { Component } from 'react';
import FormNavigator from '../../UI/FormNavigator'
import UploadPage from '../../UI/UploadPage';
import PreviewModel from '../../UI/PreviewModel';
import DescriptionPage from '../../UI/DescriptionPage';
import PricePage from '../../UI/PricePage'
import { callApi } from '../../../Helpers';
import { UserContext } from '../../Context'
import './RestaurantForm.scss'
class RestaurantForm extends Component {
  constructor(props, context) {
    super(props, context)
    this.state = {
      data: {
        hotel_id: this.context.hotel_id[0],
        parent_id: this.props.match.params.id,
        type: 1,
        categories: "['Dish']",
        content_manager: 1,
        state: 1
      },

      alert_message: [],
      sucess_message: ''
    }

    this.setData = this.setData.bind(this)
    this.send = this.send.bind(this)
  }



  setData(data) {
    this.setState({ data: { ...this.state.data, ...data } })
  }
  componentDidMount() {
    console.log(this.state.data.title_fr, 'titlefr')
    console.log(this.state.data.parent_id, "datayadata")
  }
  callbackFunction(childData) { this.state({ message: childData }) }
  async send() {
    await this.setState({ alert_message: [] })
    // if (!/(http(s?):)([/|.|\w|\s|-])*\.(?:jpg|gif|png)/.test(this.state.data.image)) {

    //   this.setState({ alert_message: [...this.state.alert_message, "Invalid image url!"] })
    // }
    if (!/[a-z]{1,10}/.test(this.state.data.title)) {

      this.setState({ alert_message: [...this.state.alert_message, "Invalid title!"] })
    }
    if (!/[a-z]{1,10}/.test(this.state.data.description)) {

      this.setState({ alert_message: [...this.state.alert_message, "Invalid description!"] })
    }
    if (!/[a-z]{1,10}/.test(this.state.data.summery)) {

      this.setState({ alert_message: [...this.state.alert_message, "Invalid summery!"] })
    }
    if (this.state.alert_message.length == 0)
      await callApi('post', this.state.data, 'POST')
    if (this.state.data.title_fr) {
      let translate_data = { post_id: this.state.data.id }
      translate_data.title = this.state.data.title_fr
      translate_data.description = this.state.data.description_fr || ''
      translate_data.lang_iso = 'fr'
      translate_data.summery = this.state.data.summery_fr || ''
      if (!/[a-z]{1,10}/.test(this.state.data.title_fr)) {

        this.setState({ alert_message: [...this.state.alert_message, "Invalid title_fr!"] })
      }
      if (!/[a-z]{1,10}/.test(this.state.data.description_fr)) {

        this.setState({ alert_message: [...this.state.alert_message, "Invalid .description_fr!"] })
      }
      if (!/[a-z]{1,10}/.test(this.state.data.summery_fr)) {

        this.setState({ alert_message: [...this.state.alert_message, "Invalid .summery_fr!"] })
      }
      if (this.state.alert_message.length == 0)
        await callApi('posts_translate', translate_data, 'POST')
      console.log(this.state.data, "Data")
      if (this.state.alert_message.length == 0)
        this.setState({ sucess_message: "success" })
    }
    // this.setState({redirect:true})
    window.location.href = new URLSearchParams(window.location.search).get('return')
  }

  render() {console.log(this.state.data,"restaurantserviceid")
    return <div>
      {/* <div className="message-succes">
        <p>{this.state.sucess_message == "success" ? "Ajout avec succes !" : null}</p>
      </div > */}
      <div className="message-erreur" >
        {this.state.alert_message.map(x => <p>{x}</p>)}
      </div>
      {/* <div className="restaurant-form-container">
        <img src="/img/ui/c15pro.png" alt="phone_photo" />
        <FormNavigator data={this.state.data} send={this.send} className="form-navigator">
          <UploadPage title='Image + Title' setData={this.setData} data={this.state.data} />
          <DescriptionPage title='Description' setData={this.setData} data={this.state.data} />
        </FormNavigator>
      </div>
      <div className="restaurant-preview-box">
        <p>{this.state.data.title}</p>
        <img src={this.state.data.imagePreviewUrl} alt="Preview Template" />
      </div> */}

      <div className="restaurant-form-container">
        <div className="left-preview">
          <div className="arrow-right" style={{ display: !this.state.data.imagePreviewUrl ? 'none' : '' }}></div>
          <img src="/img/ui/c15pro.png" alt="phone_photo" className="restaurant-c15-phone" />
          <div className="restaurant-preview-box">
            <p style={{ color: this.props.themeColor }}>{this.state.data.title}</p>
            <img src={this.state.data.imagePreviewUrl ? this.state.data.imagePreviewUrl : "/img/ui/no-photo.png"} style={{ borderLeft: !this.state.data.imagePreviewUrl ? '1px dashed gray' : '' }} alt="Preview Template" />
          </div>
        </div>

        <FormNavigator data={this.state.data} id={this.state.data.parent_id} send={this.send} setData={this.setData} className="form-navigator">
          <UploadPage title='Image + Title' setData={this.setData} data={this.state.data} />
          <DescriptionPage title='Description' setData={this.setData} data={this.state.data} />
        </FormNavigator>
      </div>

    </div>;
  }
}
RestaurantForm.contextType = UserContext

export default RestaurantForm;
