import React, { Component } from 'react';
import { callApi } from '../../Helpers';
import MessageMaintenance from './MessageMaintenance/MessageMaintenance'
import MessageRoomService from './MessageRoomService/MessageRoomService'
import MessageRestaurant from './MessageRestaurant/MessageRestaurant'
import './ListMessage.scss'
import '../../Config'
import * as firebase from 'firebase';
import {UserContext} from '../Context'
class ListMessage extends Component {
    constructor(props) {
        super(props)
        this.state = {
          gotData: data => {
            var users = data.val();
            this.setState({ users: Object.keys(users).map(x => users[x]) })
          },
          users: [] ,
          gotInfo: data =>{
            var chats = data.val();
            console.log(chats,"hhhh")
            this.setState({ chats: Object.keys(chats).map(x => chats[x]) })
          },
          chats: [] ,
          gotData: data =>{
            var tourists = data.val();
            console.log(tourists,"hhhh")
            this.setState({ tourists: Object.keys(tourists).map(x => tourists[x]) })
          },
          tourists: [] ,
        
        }
    
    
      }
      async componentDidMount() {
        firebase.database()
          .ref("Users")
          .on('value', this.state.gotData, errData);
        function errData(err) {
          console.log('Error!');
      
        }
        firebase.database()
        .ref("Chats")
        .on('value', this.state.gotInfo, errData)
        await  firebase.database()
        .ref("Tourists")
        .on('value', this.state.gotData, errData);
      function errData(err) {
        console.log('Error!');
    
      }
      }
    
   
      render() {
        const senders = this.state.chats.reduce((a, c) => {
          a[c.sender] = a[c.sender]  || { data:[]};
          a[c.sender].data.push({ sender: c.sender, message: c.message, time: c.time , receiver:c.receiver  , hotel_id:c.hotel_id});
          a[c.sender].data.sort((a, b)=> new Date(b.time) - new Date(a.time))
          return a;
        }, {})
        var messageInbox = Object.values(senders).map(s => s.data[0])
         console.log("List messages:", messageInbox);


        var messageRoomService =messageInbox.reduce((messageRoomService,item)=>{
          if (((item.receiver == "OX0pReHXfXUTq1XnOnTSX7moiGp2") &&(item.hotel_id == this.context.hotel_id[0]))) {
            messageRoomService.push(item);
          }
          return messageRoomService;
          
        }, []);

        var messageMaintenance =messageInbox.reduce((messageMaintenance,item)=>{
          if (((item.receiver == "e7NsTkSI4icHXC6c4iygbStk2Ja2") &&(item.hotel_id == this.context.hotel_id[0]))) {
            messageMaintenance.push(item);
          }
          return messageMaintenance;
          
        }, []);

        var messageRestaurant =messageInbox.reduce((messageRestaurant,item)=>{
          if (((item.receiver == "lIZmNj75abRwLDO69sCgq5eZBkC2") &&(item.hotel_id == this.context.hotel_id[0]))) {
            messageRestaurant.push(item);
          }
          return messageRestaurant;
          
        }, []);

    console.log(this.state.users,"usersmessage!!")
    console.log(messageRestaurant,"messageRestaurant!!")
    console.log(this.state.chats,"chatsmessage!!")
    console.log(this.state.tourists,"chatsmessagetourists!!")
          return <div className="list-message-container">
                <div className="title-msg">Message</div>
                <div className="msg-box">
           
                {/* {messageRoomService.map(msgr =>{
                  <MessageRoomService  msgr={msgr}/>
                })} */}

{messageRoomService
          
          .map(msgr => (
            
            <MessageRoomService  msgr={msgr} tourists={this.state.tourists} />
            
          ))
        }
        
{messageMaintenance
          
          .map(msgm => (
            
            <MessageMaintenance  msgm={msgm} tourists={this.state.tourists} />
            
          ))
        }

{messageRestaurant
          
          .map(msgre => (
            
            <MessageRestaurant  msgre={msgre} tourists={this.state.tourists} />
            
          ))
        }
         

             </div>
             <div className="view-all-msg" onClick={event =>  window.location.href='/chat'} >
                <p>view all</p> 
             </div>
 
          </div>
      }
    
    
    
    
    
    
    }
    ListMessage.contextType = UserContext
    export default ListMessage;
