import React, { Component } from 'react';
import './Switch.scss'


class Switch extends Component {

  constructor(props) {
    super(props)
  }

  render() {
    console.log(this.props.check,"this.props.check")
    return (
      
      <div className="flipswitch">
        <input type="checkbox" name="flipswitch" className="flipswitch-cb" checked={this.props.check} />
        <label  onClick={(e)=>this.props.setchek({check:this.props.check})}  className="flipswitch-label">
          <div className="flipswitch-inner"></div>
          <div className="flipswitch-switch"></div>
        </label>
      </div>
      );
  }
}

export default Switch;
