import React, { Component } from 'react';
import './DeviceForm.scss'
import { callApi } from '../../../Helpers';
import { UserContext } from '../../Context';
import Modal from 'react-awesome-modal';
/*
edit device page
..
.
.
.
.
.
.

*/
class DeviceForm extends Component {
  constructor(props, context) {
    super(props, context)
    this.state = {
      device: {},
      rooms: [],
      tourists: [],
      alert_message: [],
      visible: ''
    }
    this.edit = this.edit.bind(this)
  }
  async componentDidMount() {
    let device = await callApi('device/' + this.props.match.params.id)
    this.setState({ device: device.data[0] })
    console.log(this.state.device)
    this.setState({ room_id: device.data[0].device_room.room.room_id })
    this.setState({ tourist_id: device.data[0].device_room.stay.tourist.tourist_id })
    let rooms = await callApi('rooms', { hotel_id: this.state.device.hotel_id })
    this.setState({ rooms: rooms.data })
    let tourists = await callApi('tourists', { hotel_id: this.state.device.hotel_id })
    this.setState({ tourists: tourists.data })
    console.log(this.state)
  }
  closeModal() {
    this.setState({
      visible: false
    });
  }
  async edit() {

    if (!/^[a-zA-Z0-9_.-]*$/.test(this.state.device.imei)) {

      this.setState({ alert_message: [...this.state.alert_message, "Invalid imei!"] })
    }
    if (!/^[0-9]*$/.test(this.state.device.call_time)) {

      this.setState({ alert_message: [...this.state.alert_message, "Invalid call_time!"] })
    }
    if (!/^[0-9]*$/.test(this.state.device.call_limit)) {

      this.setState({ alert_message: [...this.state.alert_message, "Invalid call_limit!"] })
    }



    console.log("try to edit")
    let room = await callApi('deviceRoomSwitch/' + this.state.device.id, { room_id: this.state.room_id }, 'PUT')
    console.log(room, "room")
    let tourist = await callApi('deviceTouristSwitch/' + this.state.device.id, { tourist_id: this.state.tourist_id }, 'PUT')
    console.log(tourist, "tourist")
    if (this.state.alert_message.length == 0)
      if (this.state.device.imei)
        await callApi('device/' + this.state.device.id, { imei: this.state.imei }, 'PUT')
    console.log(this.state.device.imei, "imei")
    if (this.state.alert_message.length == 0)
      if (this.state.device.call_time)
        await callApi('device/' + this.state.device.id, { call_time: this.state.call_time }, 'PUT')
    if (this.state.alert_message.length == 0)
      if (this.state.device.call_limit)
        await callApi('device/' + this.state.device.id, { call_limit: this.state.call_limit }, 'PUT')
    if (this.state.alert_message.length == 0)
      if (this.state.device.intra_flotte)
        await callApi('device/' + this.state.device.id, { intra_flotte: this.state.intra_flotte }, 'PUT')
    // window.location.href = '/devices'

    if (this.state.alert_message.length == 0) {
      setTimeout(() => document.location.href = "/devices", 1000)
      this.setState({ visible: "success" })
    } else {
      this.setState({ visible: "echec" })
    }
  }





  render() {
    return <div className='device-form-edit'>
      <div className="device-header">
        <div className="title-device-edit">
          <img src="/img/ui/ajouter_client.png" alt="image" />
          <span>Device IMEI: {this.state.device.imei}</span>
        </div>

        <div className="message-erreur" >
          {this.state.alert_message.map(x => <p>{x}</p>)}
        </div>
        {this.state.visible == "success" ?
          <Modal
            className="my-modal"
            visible={this.state.visible}
            width="400"
            height="300"
            effect="fadeInUp"
            onClickAway={() => this.closeModal()}
          >
            <div className="valide-modal">
              <img src="/img/ui/succes.png" style={{ width: "32", height: "32" }} />
              <div>Successfully updated!</div>
              <button onClick={() => { this.closeModal() }} >X</button>
            </div>
          </Modal>
          : null}
        {this.state.visible == "echec" ?
          <Modal
            className="my-modal"
            visible={this.state.visible}
            width="400"
            height="300"
            effect="fadeInUp"
            onClickAway={() => this.closeModal()}
          >
            <div className="echec-modal">
              <img src="/img/ui/echec.png" style={{ width: "32", height: "32" }} />
              <div>Reinsert information please !</div>
              <button onClick={() => this.closeModal()} >X</button>
            </div>
          </Modal>
          : null}
      </div>
      <div className="device-input">
        <div>
          <label>Room:</label>
          <select onChange={e => this.setState({ room_id: e.target.value })}>
            <option selected disabled hidden>Room</option>
            {this.state.rooms.map(x => <option value={x.id}>
              {'Room ' + x.room_number + ' Section: ' + x.section + ' / Floor: ' + x.floor}
            </option>)}
          </select>
        </div>
        <div>
          <label>Tourist:</label>
          <select onChange={e => this.setState({ tourist_id: e.target.value })}>
            <option selected disabled hidden>Tourist</option>
            {this.state.tourists.map(x => <option value={x.id}>
              {x.first_name + ' ' + x.last_name}
            </option>)}
          </select>
        </div>

        {(this.context.role == 3) ? <>
          <div>
            <label htmlFor="imei">IMEI:</label>
            <input defaultValue={this.state.device.imei} id="imei" onInput={e => this.setState({ imei: e.target.value })} />
          </div>
          <div>
            <label htmlFor="call_time">Call Time:</label>
            <input defaultValue={this.state.device.call_time} id="call_time" onInput={e => this.setState({ call_time: e.target.value })} />
          </div>
          <div>
            <label htmlFor="call_limit">Call Limit:</label>
            <input defaultValue={this.state.device.call_limit} id="call_limit" onInput={e => this.setState({ call_limit: e.target.value })} />
          </div>
          <div>
            <label htmlFor="intra_flotte">Intra Flotte:</label>
            <select name="intra_flotte" onInput={e => this.setState({ intra_flotte: e.target.value })}>
              <option selected disabled hidden>Intra Flotte?</option>
              <option value="0">No</option>
              <option value="1">Yes</option>
            </select>
          </div>
        </> : null}
      </div>
      <button onClick={this.edit}>
        <img src="/img/ui/valid.png" />
        Confirm
      </button>
    </div>;
  }
}

DeviceForm.contextType = UserContext

export default DeviceForm;