import React, { Component } from 'react';
import { callApi } from '../../Helpers'
import { UserContext } from '../Context'
import './Sidebar.scss'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faSignOutAlt, faAngleRight } from '@fortawesome/free-solid-svg-icons'
import ReactSVG from 'react-svg'
import { BrowserRouter as Router, Route, NavLink } from "react-router-dom";
import { Redirect } from 'react-router-dom'
class Sidebar extends Component {
  constructor(props, context) {
    super(props)
    this.state = {
      services: [],
      expand: false,
    }
    this.logOut = this.logOut.bind(this)
  }

  async componentDidMount() {
    let post = await callApi("posts", {
      hotel_id: this.props.user.hotel_id[0] || 44,
      parent_id: null
    })
    let services = await callApi("posts", {
      hotel_id: this.props.user.hotel_id[0] || 44,
      parent_id: post.data[0].id
    })
    this.setState({ services: services.data })

  }

  logOut() {
    this.props.logOut();
  }

  render() {
    return (
      <nav>
        <div className="img-container">
          <img src="/img/login/login-logo.png" />
        </div>
        <ul className="dashboard-sidebar">
          <li className="sidebar-item">

            <ReactSVG
              src="/img/sidebar/menu.svg"
              className="svg-icon"
              id="svg-icon-dashboard"
            />
            <NavLink activeClassName="active-link" to="/">
              <p style={{ color: "white" }}>DASHBOARD
            </p>
            </NavLink>
          </li>
          <li className="sidebar-item" onClick={() => this.setState({ expand: !this.state.expand })}>
            <ReactSVG
              src="/img/sidebar/hand.svg"
              className="svg-icon"
            />
            <NavLink activeClassName="active-link" to="/service">
              <p>SERVICES
            </p></NavLink>

          </li>
          <li className={this.state.expand ? 'expanded hotel-services' : 'hotel-services'}>
            <ul>
              {this.state.services.map((x, i) => <li key={i}><NavLink activeClassName="active-link" to={'/service/' + x.id}><p>{x.title}</p></NavLink></li>)}
            </ul>
          </li>
          <li className="sidebar-item">
            <ReactSVG
              src="/img/sidebar/phone.svg"
              className="svg-icon"
            />
            <NavLink activeClassName="active-link" to="/devices">
              <p>DEVICES
            </p>
            </NavLink>
          </li>
          <li className="sidebar-item">
            <ReactSVG
              src="/img/sidebar/client.svg"
              className="svg-icon"
            />
            <NavLink activeClassName="active-link" to="/tourist">
              <p> GUESTS
            </p>
            </NavLink>
          </li>
          <li className="sidebar-item">
            <ReactSVG
              src="/img/sidebar/room.svg"
              className="svg-icon"
            />
            <NavLink activeClassName="active-link" to="/rooms">
              <p> ROOMS
            </p>
            </NavLink>
          </li>
          {(this.context.role == 3) ?
            <li className="sidebar-item">
              <ReactSVG
                src="/img/sidebar/hotel.svg"
                className="svg-icon"
              />
              <NavLink activeClassName="active-link" to="/hotels">
                <p> HOTELS
            </p>
              </NavLink>
            </li>
            : null}
          <li className="sidebar-item">
            <ReactSVG
              src="/img/sidebar/man.svg"
              className="svg-icon"
            />
            <NavLink activeClassName="active-link" to="/employees">
              <p>EMPLOYEES
              </p>
            </NavLink>
          </li>
          <li className="sidebar-item">
            <ReactSVG
              src="/img/sidebar/stats.svg"
              className="svg-icon"
            />
            <NavLink activeClassName="active-link" to="/statistics">
              <p>STATISTICS
              </p>
            </NavLink>
          </li>
          <li className="sidebar-item">
            <ReactSVG
              src="/img/ui/notifications.svg"
              className="svg-icon"
            />
            <NavLink activeClassName="active-link" to="/notifications">
              <p className='notification_style'>NOTIFICATIONS
              </p>
            </NavLink>
          </li>
          <li className="sidebar-item">
            <ReactSVG
              src="/img/ui/historique.svg"
              className="svg-icon"
            />
            <NavLink activeClassName="active-link" to="/history">
              <p>HISTORY
              </p>
            </NavLink>
          </li>
          {this.context.role == 3 ? <li className="sidebar-item">
            <ReactSVG
              src="/img/ui/hot-air-balloon.svg"
              className="svg-icon"
            />
            <NavLink activeClassName="active-link" to="/extra/28">
              <p>CITY GUIDE
              </p>
            </NavLink>
          </li> : ''}
          <li className="sidebar-item">
            <ReactSVG
              src="/img/ui/chat.svg"
              className="svg-icon"
            />
            <NavLink activeClassName="active-link" to="/chat">
              <p>CHAT
              </p>
            </NavLink>
          </li>

        </ul>
        <div className="logout-container">
          <p onClick={this.logOut}><FontAwesomeIcon color="red" icon={faSignOutAlt} rotation={180} /> Log Out</p>
        </div>
      </nav>);
  }
}

Sidebar.contextType = UserContext

export default Sidebar;
