import React, { Component } from 'react';
import { Link } from 'react-router-dom'
import "./EditButton.scss"

class EditButton extends Component {
  constructor(props) {
    super(props)
  }

  render() {
    return <div>
      <img src="/img/ui/edit_button.png" />
      <Link to={'/' + this.props.link + '/edit/' + this.props.id + "?return=" + document.location.href}></Link></div>;
  }
}

export default EditButton;
