import React, { Component } from 'react';
import FormNavigator from '../../UI/FormNavigator'
import UploadPage from '../../UI/UploadPage';
import DescriptionPage from '../../UI/DescriptionPage';
import PricePage from '../../UI/PricePage'
import { callApi } from '../../../Helpers';
import { UserContext } from '../../Context'
import './DishForm.scss';

class DishForm extends Component {
  constructor(props, context) {
    super(props, context)
    this.state = {
      data: {
        hotel_id: this.context.hotel_id[0],
        parent_id: this.props.match.params.id,
        type: 1,
        categories: "['Dish']",
        content_manager: 1,
        state: 1,

      },
    }
    this.setData = this.setData.bind(this)
    this.send = this.send.bind(this)
  }

  setData(data) {
    this.setState({ data: { ...this.state.data, ...data } })
  }

  ellipsis(promo) {
    promo = String(promo);
    return promo.substring(0, 3);
  }

  async send() {
    let x = await callApi('post', this.state.data, 'POST');
    if (this.state.data.title_fr) {
      let translate_data = { post_id: x.id }
      translate_data.title = this.state.data.title_fr
      translate_data.description = this.state.data.description_fr || ''
      translate_data.lang_iso = 'fr'
      translate_data.summery = this.state.data.summery_fr || ''
      let translate = await callApi('posts_translate', translate_data, 'POST')
    }
    window.location.href = new URLSearchParams(window.location.search).get('return')
  }

  render() {
    return <div>
      <div className="service-form-container">
        <div className="left-preview">
          <div className="arrow-right" style={{ display: !this.state.data.imagePreviewUrl ? 'none' : '' }}></div>
          <img src="/img/ui/c15pro.png" alt="phone_photo" className="c15-phone" />
          <div className="service-preview-box" style={{ borderLeft: '4px solid' + this.props.themeColor }}>
            <p style={{ color: this.props.themeColor }}>{this.state.data.title}</p>
            <img src={this.state.data.imagePreviewUrl ? this.state.data.imagePreviewUrl : "/img/ui/no-photo.png"} style={{ borderLeft: !this.state.data.imagePreviewUrl ? '1px dashed gray' : '' }} alt="Preview Template" />
            <p className="price">{this.state.data.price}<sup style={{ display: this.state.data.price ? 'inline' : 'none' }}>Dt</sup></p>
            <span className="promo" style={{ display: this.state.data.price_promo ? 'inline' : 'none' }}>
              {this.state.data.price_promo && this.state.data.price_promo.length > 3 ? this.ellipsis(this.state.data.price_promo) + '...' : this.state.data.price_promo}%</span>
          </div>
        </div>

        <FormNavigator data={this.state.data} send={this.send} className="form-navigator">
          <UploadPage title='Image + Title' setData={this.setData} data={this.state.data} />
          <DescriptionPage title='Description' setData={this.setData} data={this.state.data} />
          <PricePage title='Price' setData={this.setData} data={this.state.data} />
        </FormNavigator>
      </div>
    </div>;
  }
}
DishForm.contextType = UserContext

export default DishForm;
