import React, { Component } from 'react';
import './TopBar.scss'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faStar } from '@fortawesome/free-solid-svg-icons'
import ReactSVG from 'react-svg'
import { UserContext } from '../../Context'
import { callApi } from '../../../Helpers';
import { userInfo } from 'os';
import ListNotification from '../../ListNotification/ListNotification'
import NotificationBadge from 'react-notification-badge';
import { Effect } from 'react-notification-badge';
import ListMessage from '../../ListMessage/ListMessage';
import '../../../Config'
import * as firebase from 'firebase';
class TopBar extends Component {
  constructor(props, context) {
    super(props, context)
    this.state = {
      hotels: [],
      display: false,
      historyItems: [],
      countChat: '3',
      countMsg: '6',
      show: false,
      gotInfo: data => {
        var chats = data.val();

        this.setState({ chats: Object.keys(chats).map(x => chats[x]) })
      },
      chats: [],
    }
    this.toggle = this.toggle.bind(this)
    this.handel = this.handel.bind(this)
    this.handleOutsideClick = this.handleOutsideClick.bind(this);
    this.handleOutsideClickMsg = this.handleOutsideClickMsg.bind(this);
   
 
  }
  toggle() {
    if (!this.state.display) {
      document.addEventListener('click', this.handleOutsideClick, false);
    } else {
      document.removeEventListener('click', this.handleOutsideClick, false);
    }

    this.setState({ display: !this.state.display })
  }
  handel() {
    if (!this.state.show) {
      document.addEventListener('click', this.handleOutsideClickMsg, false);
    } else {
      document.removeEventListener('click', this.handleOutsideClickMsg, false);
    }

    this.setState({ show: !this.state.show })
  }

  handleOutsideClick(e) {
    // ignore clicks on the component itself
    if (this.node.contains(e.target)) {
      return;
    }

    this.toggle();

  }
  handleOutsideClickMsg(e) {
    // ignore clicks on the component itself
    if (this.nodeMsg.contains(e.target)) {
      return;
    }

    this.handel();
  }

  async componentDidMount() {
    if (this.context.role == 3) {
      let hotels = await callApi('hotels')
      this.setState({ hotels: hotels.data })
      this.setState({ hotels: [...this.state.hotels.filter(x => x.id == this.context.hotel_id[0]), ...this.state.hotels] })
    }
    let historyItems = await callApi('shopping_orders',{hotel_id: this.context.hotel_id[0]})
    setInterval(() =>{  this.setState({ historyItems: historyItems.data.filter(x => x.status == 1).map(x => { return { ...x, show: false } }) })}, 500);
    firebase.database()
      .ref("Chats")
      .on('value', this.state.gotInfo)
  }

  // ciuntNotif(){
  //   this.state.historyItems.filter(x=>!x.seen).length
  // }
  render() {
    const hotel = this.props.hotel;
    const post = this.props.post;
    let stars = []
    for (let i = 0; i < hotel.stars; i++)
      stars.push(<FontAwesomeIcon color="#ffde66" icon={faStar} />)
    return (
      <div className="topbar">
        <div className="topbar-posts">
          <input placeholder="Search..." />

        </div>

        <div className="topbar-hotel">

          <div onClick={this.toggle} className="list-notification"  >
            <ReactSVG className="svg-icon" src="/img/header/notif.svg" />
            <div className="notif"  >
              <NotificationBadge count={this.state.historyItems.filter(x => !x.seen).length} effect={Effect.SCALE} />
            </div>
            {this.state.display == true ?
              <div className="list-notification-position" ref={node => { this.node = node; }}>
                <ListNotification />
              </div>
              : null}
          </div>
          <div onClick={this.handel} className="chat-list" >
            <ReactSVG className="svg-icon" src="/img/header/chat.svg" />
            <div className="notif">
              <NotificationBadge count={this.state.chats.filter(x => (x.sender !== "OX0pReHXfXUTq1XnOnTSX7moiGp2") && (x.sender !== "e7NsTkSI4icHXC6c4iygbStk2Ja2") && (x.sender !== "lIZmNj75abRwLDO69sCgq5eZBkC2") && (x.isseen == "false")).length} effect={Effect.SCALE} />
            </div>
            {this.state.show == true ?
              <div className="list-msg-position" ref={nodeMsg => { this.nodeMsg = nodeMsg; }}>
                <ListMessage />
              </div>
              : null}
          </div >
          <img className="hotel-icon" src={post.cover} />
          <div className="hotel-desc">
            {this.context.role == 3 ? (
              <select onInput={(e) => this.props.changeUser(this.context, e.target.value).then(() => window.location.reload())}>
                {this.state.hotels.map(x => <option value={x.id}>{x.hotel_name}</option>)}
              </select>
            ) : <p>{hotel.hotel_name}</p>}
            {stars}
          </div>

        </div>
      </div>);
  }
}
TopBar.contextType = UserContext

export default TopBar;
