import "./DishCard.scss"
import React, { Component } from 'react';
import ReactSVG from 'react-svg'
import './DishCard.scss'
import { Redirect } from 'react-router-dom'
import Switch from '../Switch';
import EditButton from '../EditButton'
import PostInformation from "../PostInformation";

class DishCard extends Component {
  constructor(props) {
    super(props)
    this.state = {
      redirect: false,
    }
  }

  render() {
    if (this.state.redirect)
      return <Redirect to={'/dish/' + this.props.id}></Redirect>
    return <div className="dish-card">
      <div className="description">
        <ReactSVG
          src={this.props.logo}
        />
        <p className="title">
          {this.props.post.title}
        </p>
        <p className="desc">
          {this.props.post.description || 'No Description'}
        </p>
        {
          this.props.post.price ?
            (<p className="price">
              {this.props.post.price_promo ? (this.props.post.price - this.props.post.price * this.props.post.price_promo / 100).toFixed(1) : this.props.post.price.toFixed(1)}<sup> dt </sup>{this.props.post.price_promo ? '(' + this.props.post.price_promo + '%)' : ''}
            </p>) : ''
        }

      </div>
      <div style={{ backgroundImage: `url(${this.props.post.image})` }} className="image">
        <Switch id={this.props.post.id} check={this.props.post.state == 1}></Switch>
        <EditButton link={this.props.link} id={this.props.post.id}>Edit</EditButton>
        <button onClick={() => this.props.onDelete(this.props.post.id)}>Delete</button>
        <PostInformation title={this.props.title} description={this.props.description} image={this.props.image} />

      </div>
    </div>;
  }
}

export default DishCard;
