import React, { Component } from 'react';
import './AddDeviceForm.scss'
import { callApi } from '../../../Helpers';
import { UserContext } from '../../Context'
import Modal from 'react-awesome-modal';
class AddDeviceForm extends Component {
  constructor(props, context) {
    super(props, context)
    this.state = {
      device: {},
      alert_message: [],
      visible: '',
      hotels: []

    }
    this.setData = this.setData.bind(this)
    this.send = this.send.bind(this)

  }

  setData(device) {
    this.setState({ device: { ...this.state.device, ...device } })
    console.log(device, "mydevice")
  }

  async componentDidMount() {
    let hotels = await callApi("hotels")
    this.setState({ hotels: hotels.data })
    console.log(this.state.hotels.length, "rrrrrrrrrr")

  }

  async send() {
    await this.setState({ alert_message: [] })



    if (!/^[a-zA-Z0-9_.-]*$/.test(this.state.device.imei)) {

      this.setState({ alert_message: [...this.state.alert_message, "Invalid imei!"] })
    }
    if (!/^[0-9]*$/.test(this.state.device.call_time)) {

      this.setState({ alert_message: [...this.state.alert_message, "Invalid call_time!"] })
    }
    if (!/^[0-9]*$/.test(this.state.device.call_limit)) {

      this.setState({ alert_message: [...this.state.alert_message, "Invalid call_limit!"] })
    }
    if (!/^[0-9]*$/.test(this.state.device.number)) {

      this.setState({ alert_message: [...this.state.alert_message, "Invalid number!"] })
    }
    if (!/[a-z]{1,10}/.test(this.state.device.phone)) {

      this.setState({ alert_message: [...this.state.alert_message, "Invalid phone!"] })
    }

    let devices = await callApi('devices', { hotel_id: this.context.hotel_id[0] })
    for (var i = 0; i < devices.data.length; i++) {
      console.log(devices.data[i], "haho el devices")
      if (this.state.device.imei == devices.data[i].imei) {
        this.setState({ alert_message: [...this.state.alert_message, "Imei deja existe!"] })
      }
      if (this.state.alert_message.length == 0)
  
      var x= await callApi('device', this.state.device, 'POST').then(res=>{this.setState({visible: "success"})
    }).catch(error=>{ this.setState({visible: "echec"})}
    )
   
      console.log(x ,'hey')
      // if(x){
      //   this.setState({visible: "success"})
        
      //   } else {
      //   this.setState({visible: "echec"})
      //   }
   }

      var x = await callApi('device', this.state.device, 'POST')

    console.log(x, 'hey')
    if (x) {
      setTimeout(() => document.location.href = "/devices", 1000)
      this.setState({ visible: "success" })

    } else {
      this.setState({ visible: "echec" })
    }

  }

  closeModal() {
    this.setState({
      visible: false
    });
  }
  render() {
    var hotelName = [];
    for (var i = 0; i < this.state.hotels.length; i++) {
      for (var j = 0; j < this.context.hotel_id.length; j++) {
        if (this.state.hotels[i].id == this.context.hotel_id[j]) {
          var nameHotel = this.state.hotels[i].hotel_name
          var hotelid = this.context.hotel_id[j]
          hotelName.push({ nameHotel, hotelid })
        }
      }
    }
    return <div className='device-form-adds'>
      <div className="device-header">
        <div className="title-device">
          <div><img src="/img/ui/ajouter_client.png" alt="phone_photo" /></div>
          Add Device
      </div>

      </div>

      <div className="message-erreur" >
        {this.state.alert_message.map(x => <p>{x}</p>)}
      </div>
      {this.state.visible == "success" ?
        <Modal
          className="my-modal"
          visible={this.state.visible}
          width="400"
          height="300"
          effect="fadeInUp"
          onClickAway={() => this.closeModal()}
        >
          <div className="valide-modal">
            <img src="/img/ui/succes.png" style={{ width: "32", height: "32" }} />
            <div>successfully added !</div>
            <button onClick={() => this.closeModal()} >X</button>
          </div>
        </Modal>
        : null}
      {this.state.visible == "echec" ?
        <Modal
          className="my-modal"
          visible={this.state.visible}
          width="400"
          height="300"
          effect="fadeInUp"
          onClickAway={() => this.closeModal()}
        >
          <div className="echec-modal">
            <img src="/img/ui/echec.png" style={{ width: "32", height: "32" }} />
            <div>Reinsert information please !</div>
            <button onClick={() => this.closeModal()} >X</button>
          </div>
        </Modal>
        : null}
      <div className="device-inputs">
        <input value={this.state.imei} type="number" required placeholder="IMEI" onChange={(e) => { this.setData({ imei: e.target.value.substr(0, 15) }); this.setState({ imei: e.target.value.substr(0, 15) }) }} />
        <input type="number" required placeholder="Call Time" onChange={(e) => this.setData({ call_time: e.target.value })} />
        <input type="number" required placeholder="Call Limite" onChange={(e) => this.setData({ call_limit: e.target.value })} />
        <input type="number" required placeholder="SIM Number" onChange={(e) => this.setData({ number: e.target.value })} />
        <input type="text" required placeholder="Phone Description" onChange={(e) => this.setData({ phone: e.target.value })} />
        <select name="hotel_id" onChange={(e) => this.setData({ hotel_id: e.target.value })}>
          <option selected disabled hidden>Hotel Name</option>
          {hotelName.map(x => <option value={x.hotelid}>{x.nameHotel}</option>)}
        </select>
        <select onChange={(e) => this.setData({ intra_flotte: e.target.value })}>
          <option selected disabled hidden>Intra Flotte?</option>
          <option value="0">No</option>
          <option value="1">Yes</option>
        </select>
        <select onChange={(e) => this.setData({ status: e.target.value })}>
          <option selected disabled hidden>Status</option>
          <option value="0">Inactive</option>
          <option value="1">Active</option>
        </select>
        <button onClick={this.send}>
          <img src="/img/ui/valid.png" />
          Confirm
      </button>
      </div>

    </div>;
  }
}

AddDeviceForm.contextType = UserContext

export default AddDeviceForm;