import React, { Component } from 'react';
import { callApi } from '../../Helpers';
import { UserContext } from '../Context';
import Switch from '../UI/Switch'
import ServiceCard from '../UI/ServiceCard/ServiceCard';
import ReactSVG from 'react-svg'
import './Home.css';
import { Redirect } from 'react-router-dom';
import AddButton from '../UI/AddButton/AddButton';

class Services extends Component {

  constructor(props, context) {
    super(props, context)
    console.log(this.context)
    this.state = {
      services: [
        { parent_id: 1 }
      ],
      check: false,
      redirect: false,
    }

    this.check = this.check.bind(this)
    this.delete = this.delete.bind(this)
  }

  delete(id){
    this.setState({services: this.state.services.filter(x=>x.id!=id)})
  }

  check() {
    this.setState({ check: !this.state.check })
  }

  async componentDidMount() {
    let post = await callApi("posts", {
      hotel_id: this.context.hotel_id[0],
      parent_id: null
    })
    console.log(post, "Current hotel info")
    let services = await callApi("posts", {
      hotel_id: this.context.hotel_id[0],
      parent_id: post.data[0].id
    })
    this.setState({ services: services.data })

  }

  render() {

    return (
      <div className="content">
        <AddButton link="service" id={this.state.services[0].parent_id} return={window.location.href} services={this.state.services} style={{ width: ' 24%',/*minWidth:'450px',*/height: ' 130px', border: '1px dashed #7a8da1', marginLeft: '-2px' }}>
          <ReactSVG src="/img/restaurant/plus.svg" />
          <p>
            Add
        </p>
        </AddButton>
        {this.state.services.map((x, i) => {
          return (
            <ServiceCard service={x} id={x.id} check={x.state == 1}  index={i}/>
          )
        })}
      </div>
    );
  }
}

Services.contextType = UserContext

export default Services;
