import React, { Component } from 'react';
import { Link } from 'react-router-dom'
import "./EmployeeCard.scss"
import ReactSVG from 'react-svg';
import { callApi } from '../../../Helpers';
import Popup from "reactjs-popup";

class EmployeeCard extends Component {
  async onDelete(id){

    let x = await callApi('hotel/'+id, 'DELETE')
    console.log(x)
    console.log("room delete")
  }

  render() {
    return <div className="employee-card">
    <div className="employee-details">
      <div className="image"><img src="/img/ui/employes.png" /></div>
      <p>{this.props.data.name}</p>
    </div>
    <div className="employee-button">
    <div className="employee-redirect">
      <Link to={"/employee/history/"+this.props.data.id}>
        <button className="history_butt">
          <div className="history_image"><img src="/img/ui/hhistorique.png"/></div>
          
          <div className="text">History</div>
          
          </button>
      </Link>
      <Link to={"/emlpoyee/edit/"+this.props.data.id}>
      <button className="edit_butt">
          <div className="edit_image"><img src="/img/ui/modifier.png"/></div>
          
          <div className="text">Edit</div>
          
          </button>
      </Link>
      </div>
      <Popup trigger={<div>  <ReactSVG src="/img/ui/delete.svg"/> </div>} modal>
    
    {close => (
      <div className="modal"  >
       
        <div className="header">Are you sure you want to delete? </div>
            
        <div className="actions">
          <img src="/img/ui/btn_valider.png" onClick={this.onDelete.bind(this,this.props.data.id)} /> 
          <img src="/img/ui/btn_annuler.png"  onClick={() => {console.log("modal closed "); close();}}/>
        </div>
      </div>
    )}
   
  </Popup> 
      {/* <div onClick={async ()=>callApi('/user/'+this.props.data.id,{},'DELETE')}>
      <ReactSVG src="/img/ui/delete.svg"/>
      </div> */}
    </div>
    </div>
  }
}

export default EmployeeCard;
