import React, { Component } from 'react';
import './EmployeeForm.scss'
import { callApi } from '../../../Helpers';
import { UserContext } from '../../Context'

class EmployeeForm extends Component {
  constructor(props, context) {
    super(props, context)
    this.state = {
      role: 2,
      approved: 1,
      hotel_id: this.context.hotel_id[0]

    }
    this.edit = this.edit.bind(this)
  }

  async edit() {

    let x = await callApi('user',
      {
        ...this.state
      },
      'POST')

    // if(x.message)
    //   window.location.href="/employees"
    //   else
    //   alert("There has been an error please try again")
  }
  render() {

    return <div className='employee-form-add'>
      <div className="employee-header">
        <div className="employee-header_title">Add Employee</div>
      </div>
      <div className="employee-inputs add">
        <input placeholder="Name" onInput={e => this.setState({ name: e.target.value })} />
        <input type="text" placeholder="Email" onInput={e => this.setState({ email: e.target.value })} />
        <input type="password" placeholder="Password" onInput={e => this.setState({ password: e.target.value })} />
        <button onClick={this.edit}>
          <img src="/img/ui/valid.png" />
          Add
        </button>
      </div>

    </div>;
  }
}

EmployeeForm.contextType = UserContext

export default EmployeeForm;
